import React from "react";
import "bulma/css/bulma.css";
import { v4 as uuidv4 } from "uuid";

class App1 extends React.Component {
  constructor(args) {
    super(args);

    this.state = {
      todos: [],
      newTodoVal: "",
    };
  }
  handleInput = (evt) => {
    this.setState({ newTodoVal: evt.target.value });
  };
  handleDelete = (title) => {
    this.setState({
      todos: this.state.todos.filter((x) => x.title !== title),
    });
  };
  handleComplete = (id) => {
    this.setState({
      todos: this.state.todos.map((x) => {
        if (x.id === id) {
          x.done = true;
        }
        return x;
      }),
    });
  };
  handleSubmit = (evt) => {
    let todos = this.state.todos;
    todos.push({
      id: uuidv4(),
      title: this.state.newTodoVal,
      done: false,
    });
    this.setState({
      todos,
      newTodoVal: "",
    });
    evt.preventDefault();
  };
  render() {
    return (
      <div className="container">
        <h1 className="title">Todo List</h1>
        <hr></hr>
        {this.state.todos.map((x) => {
          return (
            <div className="columns" data-id={x.id} key={x.id}>
              <div className="column is-one-third">
                {x.title} -{" "}
                {x.done ? (
                  <span className="has-text-success">Done!</span>
                ) : (
                  <span className="has-text-danger">Not done</span>
                )}
              </div>
              <div className="column columns is-one-thid">
                <div className="column is-one-fifth">
                  <button
                    className="button is-success"
                    onClick={() => this.handleComplete(x.id)}
                  >
                    Complete
                  </button>
                </div>
                <div className="column is-one-fifth">
                  <button
                    className="button is-danger"
                    onClick={() => this.handleDelete(x.title)}
                  >
                    Delete
                  </button>
                </div>
              </div>
            </div>
          );
        })}
        <form id="add-todo" onSubmit={this.handleSubmit}>
          <div className="columns">
            <div className="column is-one-third">
              <input
                className="input"
                type="text"
                value={this.state.newTodoVal}
                onChange={this.handleInput}
              />
            </div>
            <div className="column is-one-third">
              <button className="button">Add+</button>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

export default App1;
